import React from 'react';
import {users} from './users';

const User = () => {
    const [value, setValue] = React.useState(users.length);
    const [person, setPerson] = React.useState(users);
    const deleteAll = () =>{
        setPerson([]);
        setValue(0);
    }
    const removePerson = (id) =>{
        const newPerson = person.filter((name) =>{
            return name.id !== id;
        });
        setPerson(newPerson);
        setValue(newPerson.length);
    }
    return (
        <>
        <div className="main">
            <section className="container">
                <h2>{value} birthdays today</h2>
                {person.map((employee) => {
                    //const [img, name, age, id] = employee;
                    return(
                        <article className="user" key={employee.id}>
                            <img src={employee.img} alt={employee.name} />
                            <div className="info">
                                <div>
                                    <h4>{employee.name}</h4>
                                    <p>{employee.age}</p>
                                </div>
                                <div className="align-right">
                                    <button className="remove" onClick={() => removePerson(employee.id)}>remove</button>
                                </div>
                            </div>
                        </article>
                    );
                })}
                <button className="btn" onClick={deleteAll}>Clear All</button>
            </section>
        </div>
        </>
    )
}
export default User;